import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import backendUrl from "../../backendUrl/backendUrl";
import axios from "axios";
const initialState = {
    products: [],
    status: 'idle',
    selectedProduct: null,
    brands: [],
    categories: [],
    totalItems: 0,
}

export const fetchAllProductsAsync = createAsyncThunk("fetchAllProductsAsync", async () => {
    const response = await axios.get(`${backendUrl}/products/all-products`);
    return response.data;
})

export const fetchFilteredAsync = createAsyncThunk("fetchFilteredAsync", async ({ filter, sort, pagination }) => {
    try {
        const response = await axios.post(`${backendUrl}/products/filtered-products`, { filter, sort, pagination });
        return response.data;
    } catch (error) {
        console.error(error);
        throw error; // Re-throw the error to be handled by the component
    }
});

export const fetchProductByIdAsync = createAsyncThunk("fetchProductByIdAsync", async (productId) => {
    const response = await axios.get(`${backendUrl}/products/product/${productId}`);
    return response.data;
})

export const fetchBrandsAsync = createAsyncThunk("fetchBrandsAsync", async () => {
    const response = await axios.get(`${backendUrl}/brands/fetch-brands`);
    return response.data;
})

export const fetchCategoriesAsync = createAsyncThunk("fetchCategoriesAsync", async () => {
    const response = await axios.get(`${backendUrl}/categories/fetch-categories`);
    return response.data;
})



export const productSlice = createSlice({
    name: 'product',
    initialState,
    reducers: {
        clearSelectedProduct: (state) => {
            state.selectedProduct = null
        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(fetchAllProductsAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchAllProductsAsync.fulfilled, (state, action) => {
                // console.log('Fetched Products:', action.payload);
                state.status = 'idle';
                state.products = action.payload;
                // state.totalItems = action.payload.length;
            })
            .addCase(fetchFilteredAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchFilteredAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.products = action.payload.products;
                state.totalItems = action.payload.totalItems;
            })
            .addCase(fetchProductByIdAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchProductByIdAsync.fulfilled, (state, action) => {
                // console.log('Fetched Products:', action.payload);
                state.status = 'idle';
                state.selectedProduct = action.payload;
            })
            .addCase(fetchBrandsAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchBrandsAsync.fulfilled, (state, action) => {
                // console.log('Fetched Products:', action.payload);
                state.status = 'idle';
                state.brands = action.payload;
            })
            .addCase(fetchCategoriesAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchCategoriesAsync.fulfilled, (state, action) => {
                // console.log('Fetched Products:', action.payload);
                state.status = 'idle';
                state.categories = action.payload;
            })
    }
})
export const { clearSelectedProduct } = productSlice.actions;
export const selectAllProducts = (state) => state.product.products;
export const selectProductById = (state) => state.product.selectedProduct;
export const selectBrands = (state) => state.product.brands;
export const selectCategories = (state) => state.product.categories;
export const selectTotalItems = (state) => state.product.totalItems;
export default productSlice.reducer;