import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { useForm } from "react-hook-form";
import backendUrl from "../backendUrl/backendUrl";
import Swal from "sweetalert2";
import { useDispatch } from "react-redux";
import { createUserAsync } from "../redux/slices/authSlice";
function Copyright(props) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"Copyright © "}
      <Link color="inherit" href="https://github.com/omjadav">
        OM JADAV
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

// TODO remove, this demo shouldn't need to reset the theme.

export default function Signup() {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const dispatch = useDispatch();

  // try {
  //   const response = await axios.post(`${backendUrl}/signup`, data);
  //   // Swal.fire("Success", response.data.message, "success").then(
  //   // () => (window.location.href = "/login")
  //   // );

  //   console.log("User registered successfully:", response.data);
  // } catch (error) {
  //   // Swal.fire(error.response.data.error, "Sign up failed", "error");
  //   console.log("Error registering user axios:", error);
  // }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Create An Account
        </Typography>
        <Box
          component="form"
          onSubmit={handleSubmit((data) => {
            console.log(data);
            dispatch(
              createUserAsync({
                name: data.name,
                email: data.email,
                password: data.password,
                addresses: [],
              })
            );
          })}
          noValidate
          sx={{ mt: 1 }}
        >
          <TextField
            margin="normal"
            required
            fullWidth
            id="name"
            label="Name"
            name="name"
            {...register("name", { required: "Name is Required" })}
            autoFocus
            type="text"
          />

          {errors.name && <p className="text-red-500">{errors.name.message}</p>}
          <TextField
            margin="normal"
            required
            fullWidth
            id="email"
            type="email"
            label="Email Address"
            name="email"
            {...register("email", {
              required: "Email is Required",
              pattern: {
                // value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/gi,
                value: /\b\w+@[\w.-]+\.\w{2,4}\b/gi,
                message: "email not valid",
              },
            })}
            autoComplete="email"
          />

          {errors.email && (
            <p className="text-red-500">{errors.email.message}</p>
          )}
          <TextField
            margin="normal"
            required
            defaultValue={""}
            fullWidth
            name="password"
            label="Password"
            type="password"
            {...register("password", {
              minLength: {
                value: 4,
                message: "Minimum 4 characters for password",
              },
              required: "password is required",
            })}
            id="password"
          />
          {errors.password && (
            <p className="text-red-500">{errors.password.message}</p>
          )}
          <TextField
            margin="normal"
            required
            defaultValue={""}
            fullWidth
            name="confirm_password"
            label="Confirm Password"
            type="password"
            {...register("confirm_password", {
              validate: (value, formValues) =>
                value === formValues.password || "password not matching",
            })}
            id="confirm_password"
          />
          {errors.confirm_password && (
            <p className="text-red-500">{errors.confirm_password.message}</p>
          )}

          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Register
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                {""}
              </Link>
            </Grid>
            <Grid item>
              <Link href="/login" variant="body2">
                {"Already have an Account? Login"}
              </Link>
            </Grid>
          </Grid>
        </Box>
      </Box>
      <Copyright sx={{ mt: 8, mb: 4 }} />
    </Container>
  );
}
