import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import backendUrl from "../../backendUrl/backendUrl";
const initialState = {
    items: [],
    status: 'idle',
    value: false,
}


export const addToCartAsync = createAsyncThunk("addToCartAsync", async (newItem) => {
    try {
        console.log(newItem);
        const response = await axios.post(`${backendUrl}/cart/addToCart`, newItem);
        console.log("Item added in cart successfully:", response.data);
        return response.data;
    } catch (error) {
        console.error("Error adding in cart axios:", error.response?.data || error.message);
    }
})

export const fetchItemsByUserIdAsync = createAsyncThunk("fetchItemsByUserId", async ({ userId }) => {
    try {
        const response = await axios.get(`${backendUrl}/cart/get-cart-by-user`, { params: { userId } })
        return response.data;
    } catch (error) {
        console.error("Error adding in cart fetching by ID axios:", error.response?.data || error.message);

    }
})

export const updateCartAsync = createAsyncThunk("updateCartAsync", async (update) => {
    try {
        // console.log("Updating cart:", update);
        const response = await axios.post(`${backendUrl}/cart/updatecart/${update.id}`, { quantity: update.quantity });
        // console.log("Updated cart response:", response.data);
        return response.data;
    } catch (error) {
        console.error("Error updating cart axios:", error.response?.data || error.message);
    }
})


export const deleteItemFromCartAsync = createAsyncThunk("deleteItemFromCartAsync", async (id) => {
    try {
        const response = await axios.delete(`${backendUrl}/cart/delete-cart-item/${id}`)
        return response.data;
    } catch (error) {
        console.error("Error deleteing cart item axios:", error.response?.data || error.message);
    }
})


export const cartSlice = createSlice({
    name: 'cart',
    initialState
    , reducers: {
        showCart: (state) => { state.value = true },
        closeCart: (state) => { state.value = false },
    },
    extraReducers: (builder) => {
        builder
            .addCase(addToCartAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(addToCartAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.items.push(action.payload);
            })
            .addCase(fetchItemsByUserIdAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchItemsByUserIdAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.items = action.payload;
            })
            .addCase(updateCartAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(updateCartAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                const index = state.items.findIndex(item => item._id === action.payload._id)
                state.items[index] = action.payload;
            })
            .addCase(deleteItemFromCartAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(deleteItemFromCartAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                const index = state.items.findIndex(item => item._id === action.payload._id)
                state.items.splice(index, 1);
            })
    }
})

export const { showCart, closeCart } = cartSlice.actions;
export const selectItems = (state) => state.cart.items;
export default cartSlice.reducer;
