import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import backendUrl from "../../backendUrl/backendUrl";

const initialState = {
    orders: [],
    status: 'idle',
    currentOrder: null,
    totalOrders: 0,
};

export const createOrderAsync = createAsyncThunk("createOrderAsync", async (order) => {
    console.log(order);
    try {
        const response = await axios.post(`${backendUrl}/order/neworder`, order);
        return response.data
    } catch (error) {
        console.error("Error creating order axios:", error.response?.data || error.message);
    }
})

export const orderSlice = createSlice({
    name: 'order',
    initialState,
    reducers: {
        resetOrder: (state) => {
            state.currentOrder = null;
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(createOrderAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(createOrderAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.orders.push(action.payload);
                state.currentOrder = action.payload;
            })

    }
})

export const { resetOrder } = orderSlice.actions;

export const selectCurrentOrder = (state) => state.order.currentOrder;
export const selectOrders = (state) => state.order.orders;
export const selectTotalOrders = (state) => state.order.totalOrders;
export const selectStatus = (state) => state.order.status;

export default orderSlice.reducer;