import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import backendUrl from '../../backendUrl/backendUrl';
import axios from 'axios';
import Swal from 'sweetalert2';

const initialState = {
    loggedInUser: null,
    status: 'idle',
};

export const createUserAsync = createAsyncThunk("createUserAsync", async (data) => {
    try {
        const response = await axios.post(`${backendUrl}/auth/signup`, data);
        Swal.fire("Success", response.data.message, "success").then(
            () => (window.location.href = "/login")
        );
        console.log("User registered successfully:", response.data);
    } catch (error) {
        Swal.fire(error.response.data.error, "Sign up failed", "error");
        console.log("Error registering user axios:", error);
    }
})
export const loginUserAsync = createAsyncThunk("loginUserAsync", async (data) => {
    try {
        const response = await axios.post(`${backendUrl}/auth/login`, data);
        console.log("User Logged in successfully:", response.data);
        localStorage.setItem("currentUser", JSON.stringify(response.data.user))
        window.location.href = '/'
        // return response.data
    } catch (error) {
        console.error("Error Logged in user axios:", error);
    }
})


export const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(createUserAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(createUserAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.loggedInUser = action.payload;
            })
            .addCase(loginUserAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(loginUserAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.loggedInUser = action.payload;
            })

    },
});

export const selectLoggedInUser = (state) => state.auth.loggedInUser;

// export const { } = authSlice.actions;

export default authSlice.reducer;