import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import backendUrl from "../../backendUrl/backendUrl";

const initialState = {
    status: 'idle',
    userInfo: {},
}

export const fetchLoggedInUserOrderAsync = createAsyncThunk("fetchLoggedInUserOrderAsync", async (id) => {
    try {
        const response = await axios.get(`${backendUrl}/order/user-orders/${id}`)
        return response.data
    } catch (error) {
        console.error("Error getting orders by user axios:", error.response?.data || error.message);
    }
})

export const fetchLoggedInUserAsync = createAsyncThunk("fetchLoggedInUserAsync", async (userId) => {
    try {
        const response = await axios.get(`${backendUrl}/user/own/${userId}`)
        return response.data
    } catch (error) {
        console.error("Error getting logged in user axios:", error.response?.data || error.message);
    }
})

export const updateUserAsync = createAsyncThunk("updateUserAsync", async (update) => {
    try {
        const response = await axios.patch(`${backendUrl}/user/${update.id}`, update)
        return response.data
    } catch (error) {
        console.error("Error updating user axios:", error.response?.data || error.message);
    }
})


export const userSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {

    },
    extraReducers: (builder) => {
        builder
            .addCase(fetchLoggedInUserOrderAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchLoggedInUserOrderAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.userInfo.orders = action.payload;
            })
            .addCase(updateUserAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(updateUserAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                // earlier there was loggedInUser variable in other slice
                state.userInfo = action.payload;
            })
            .addCase(fetchLoggedInUserAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchLoggedInUserAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                // this info can be different or more from logged-in User info
                state.userInfo = action.payload;
            });
    }
})

export const selectUserOrders = (state) => state.user.userInfo.orders;
export const selectUserInfo = (state) => state.user.userInfo;
export const selectUserInfoStatus = (state) => state.user.status;

export default userSlice.reducer;