import React from "react";
import { Link } from "react-router-dom";
import discountedPrice from "../../functions/discountedPrice";
function Products({ products }) {
  return (
    <>
      <div>
        <div className="bg-white">
          <div className="mx-auto max-w-2xl px-4  sm:px-6 lg:max-w-7xl lg:px-5">
            {/* <h2 className="text-2xl font-bold tracking-tight text-gray-900">
              Customers also purchased
            </h2> */}

            <div className="mt-6 grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8 ">
              {/* Product card starts*/}
              {products.map((product) => (
                <Link to={`/product/${product._id}`} key={product._id}>
                  <div className="group relative border rounded-md shadow shadow-slate-950  p-1">
                    <div className="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-md bg-gray-200 lg:aspect-none group-hover:opacity-75 lg:h-80">
                      <img
                        src={product.thumbnail}
                        alt={product.title}
                        className=" object-cover object-center h-full w-full lg:h-full lg:w-full"
                      />
                    </div>
                    {/* <div className="  bg-yellow-500 text-white py-1 px-2 m-2 rounded-md">
                        {product.discountPercentage}% OFF
                    </div> */}
                    <div className="absolute top-2 left-2 inline-flex items-center rounded-md bg-yellow-500 px-2 py-1 text-xs font-medium text-white ring-1 ring-inset ring-yellow-400">
                      {product.discountPercentage}% OFF
                    </div>
                    <div className="mt-4 flex justify-between mr-1">
                      <div>
                        {" "}
                        <h3 className="text-sm font-bold text-gray-700">
                          <span
                            aria-hidden="true"
                            className="absolute inset-0"
                          />
                          {product.title}
                        </h3>
                        <p className="mt-1 text-sm text-gray-500 flex items-center">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                            strokeWidth={1.5}
                            stroke="currentColor"
                            className="w-6 h-6 mr-1 text-yellow-400 fill-yellow-400"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              d="M11.48 3.499a.562.562 0 0 1 1.04 0l2.125 5.111a.563.563 0 0 0 .475.345l5.518.442c.499.04.701.663.321.988l-4.204 3.602a.563.563 0 0 0-.182.557l1.285 5.385a.562.562 0 0 1-.84.61l-4.725-2.885a.562.562 0 0 0-.586 0L6.982 20.54a.562.562 0 0 1-.84-.61l1.285-5.386a.562.562 0 0 0-.182-.557l-4.204-3.602a.562.562 0 0 1 .321-.988l5.518-.442a.563.563 0 0 0 .475-.345L11.48 3.5Z"
                            />
                          </svg>
                          {product.rating}
                        </p>
                      </div>
                      <div>
                        <p className=" font-medium text-gray-900">
                          ₹{" "}
                          {discountedPrice(
                            product.price,
                            product.discountPercentage
                          )}
                        </p>
                        <p className="text-sm  line-through">
                          ₹ {product.price}
                        </p>
                      </div>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Products;
